h1.title {
    color: white;
    padding-left: 10px;
    display: inline-block;
    font-family: Montserrat-Regular;
}

.home-container {
    display: flex;
    /* establish flex container */
    flex-direction: column;
    /* make main axis vertical */
    justify-content: center;
    /* center items vertically, in this case */
    align-items: center;
    /* center items horizontally, in this case */
    min-height: 100vh;
    max-width: 100vw;
    margin: 0px;
    background-color: black;
    padding: 30px 0;
}

.home-box {
    width: 700px;
    margin: 10px;
    text-align: center;
    /* will center text in <p>, which is not a flex item */
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-button {
    background-color: #420000;
    color: white;
    height: 140px;
    width: 150px;
    margin: 0 20px 0 20px;
    transition: 0.5s;
    border-color: black;
}

.home-box .icons {
    /* height: 150px;
  width: 150px; */
    margin: 17px 0px 0px 0px;
    /* color: black */
}

svg {
    transition: 0.2s;
}

form {
    font-family: Montserrat-Light;
}

.home-button:hover svg,
.home-button:hover {
    fill: rgb(255, 203, 30);
    /*svg color change on button hover*/
}

.home-button:hover {
    background-color: black;
    border-color: rgb(255, 203, 30);
    color: rgb(255, 203, 30);
}

.home-button:focus {
    background-color: #420000;
    border-color: rgb(255, 203, 30);
}

.home-button:active {
    /* background-color: #420000; */
    color: rgb(255, 203, 30);
}

.home-button p {
    margin-top: 10px;
    font-family: Montserrat-ExtraLight;
    font-size: 90%;
}

.box img {
    margin: 10px;
}


