/*Profile*/

.grid-container {
    display: grid;
    /* establish flex container */
    justify-content: center;
    /* center items vertically, in this case */
    align-items: center;
    /* center items horizontally, in this case */
    height: 100vh;
    max-width: 100vw;
    margin: 0px;
    background-color: black;
    grid-template-columns: 50% 50%;
    grid-template-rows: 75% auto;
    padding-top: 56px;
}

.profile-base {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 3;
    display: grid;
    grid-template-rows: 2fr 1fr 1fr;
    color: white;
    border-right: 1px solid white;
    /* margin-bottom: auto;
    margin-top: auto; */
}

.profile-base svg {
    padding-bottom: 5px;
}

.profile-base img {
    margin-bottom: 20px;
}

.profile-base button {
    pointer-events: none;
    /* display: inline; */
    width: 50px;
    height: 30px;
    text-align: center;
    margin-left: 10px;
}

.profile-about {
    display: grid;
    align-self: flex-end;
    color: white;
    /* padding: 90px 50px 0 30px; */
    padding: 0px 50px 0 30px;
    grid-row-start: 1;
    grid-row-end: 3;
    align-self: center;
    justify-self: center;
}

.profile-about h2 {
    display: inline;
    justify-self: center;
}

.profile-about a {
    color: white;
    text-decoration: underline;
    margin-left: 0px;
    font-size: 80%;
}

.add-info {
    margin-left: 30px;
    line-height: 1.5;
    word-break: break-all
}

.profile-base h5 {
    display: inline;
}

.profile-about em {
    font-weight: bold;
}

.profile-about table {
    width: 100%;
    margin-left: 30px;
}

.profile-about tr {
    line-height: 2.5;
}

.profile-links {
    align-self: center;
    padding-left: 0;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
}

.profile-links button {
    margin-right: 20px;
    background-color: #420000;
    border-color: white;
}

.profile-links button {
    margin-right: 20px;
    background-color: #420000;
    border-color: white;
}

.profile-links button:hover {
    border-color: rgb(255, 203, 30);
    color: rgb(255, 203, 30);
    background-color: black;
}

.profile-base h5 {
    margin-right: 10px;
}

.switch.btn {
    min-width: 3.7rem;
    min-height: 2rem;
}


